.root::-webkit-scrollbar {
  width: 5px;
}
.root::-webkit-scrollbar-track {
  background: $overlay-scroll;
}
.root::-webkit-scrollbar-thumb {
  background: $accent-secondary;
  border: 3px solid $accent-secondary;
  border-radius: 5px;
}

.root__modal-open {
  height: 100vh;
  overflow: hidden;
}
